@import url(https://fonts.googleapis.com/css?family=Raleway:200,400|PT+Mono&display=swap);
body {
  margin: 0;
  font-family: 'Raleway', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}

code {
  font-family: 'Courier New', Courier, monospace;
}

#dashboard-container {
  position: relative;
  width: 100vw;
  height: 100vh;
  border: 1px solid #d8dbe2;
}
h1 {
  margin: 0;
  font-size: 1.15em;
  color: #fff;
  letter-spacing: 2px;
  text-transform: uppercase;
}
p {
  color: #577399;
  margin: 0;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 0.8em;
}
p a {
  text-decoration: none;
  color: #577399;
}
.main-grid {
  width: 60vw;
  margin: 0px 0px 20px 300px;
  padding-top: 20px;
  background: #fff;
  height: 95vh;
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(2, 1fr);
  grid-template-areas: "one four" "one four" "two five" "two five" "two five" "two five" "two five" "three five";
}
.icon {
  width: 55px;
  height: 55px;
  background: #b7cee8;
  border-radius: 50%;
}
.icon p {
  color: #fff;
  font-size: 1.7em;
  margin: 0;
  margin-top: 12px;
}
.item {
  border: 1px solid #d8dbe2;
}
.content {
  max-height: 100vh;
  height: 100vh;
}

.leftside-menu {
    position: absolute;
    height: 100%;
    width: 150px;
    background: #d8dbe2;
    box-shadow: 2px 2px 15px rgba(73, 88, 103, .3);
    text-align: center;
    top: 0;
    bottom: 0;
}
#greetingContainer {
    width: 90%;
}
.user {
    margin-top: 55%;
    margin-bottom: 25%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.greeting {
    color: #495867;
    font-size: 0.8em;
    margin-top: 15px;
    padding: 5px;
    border: 1px solid #495867;
    text-transform: uppercase;
}
input {
    border: 0;
    padding: 0;
    margin: 0;
    background: transparent;
    font: 18px Avenir;
    border-bottom: 1px solid #495867;
    width: 135px;
}
.text {
    color: #495867;
}
a {
    display: block;
    color: #495867;
    margin: 0;
    padding: 15px;
    text-transform: uppercase;
    font-size: 0.8em;
    letter-spacing: 1px;
    cursor: pointer;
}
a:hover {
    background: #b7cee8;
    color: #577399;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}
.active {
    background: #b7cee8;
    color: #fff;
    width: 140px;
    margin-left: -10px;
    box-shadow: 1px 1px 3px rgba(73, 88, 103, .3);
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}
.active:hover {
    background: #fff;
    color: #495867;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}
a {
    text-decoration: none;
}


.two {
    grid-area: two;
    grid-column: 1;
    border: 1px solid #d8dbe2;
    text-align: center;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.music {
    text-align: center;
    color: #577399;
    margin: 10px 0 0 0;
    text-transform: uppercase;
}
.info {
    height: 5px;
    background: rgba(255, 255, 255, .85);
    -webkit-transform: translateY(35px);
            transform: translateY(35px);
    -webkit-transition: all 0.5s ease-in-out;
    transition: all 0.5s ease-in-out;
}
.progress-bar {
    height: 5px;
    width: 60%;
    margin: 6% auto;
    background: #e7e9ed;
    border-radius: 10px;
    font-family: 'Raleway', sans-serif;
}
.fill {
    background-color: #b7cee8;
    width: 40%;
    height: 0.3rem;
    border-radius: 2px;
}
.time--current, .time--total {
    color: #495867;
    font-size: 10px;
    position: absolute;
    margin-top: -4px;
}
.time--current {
    left: 15px;
}
.time--total {
    right: 15px;
}
.currently-playing {
    text-align: center;
    margin-top: -3px;
}
.song-name, .artist-name {
    font-family: 'Raleway', sans-serif;
    text-transform: uppercase;
    margin: 0;
}
.song-name {
    font-size: 0.8em;
    letter-spacing: 3px;
    color: #495867;
}
.artist-name {
    font-size: 0.6em;
    letter-spacing: 1.5px;
    color: #697e94;
    margin-top: 5px;
}
.controls {
    display: flex;
    align-items: center;
    font-size: 0.8em;
    justify-content: center;
    color: #b7cee8;
    cursor: pointer;
}
.controls .play {
    margin: 15px 20px;
    color: #90b4dc;
}
.controls .option {
    left: 10px;
    position: absolute;
    font-size: 0.8em;
}
.controls .add {
    right: 10px;
    position: absolute;
    font-size: 0.8em;
}
.controls .volume {
    margin-right: 30px;
    font-size: 0.8em;
}
.controls .shuffle {
    margin-left: 30px;
    font-size: 0.8em;
}
.play, .next, .previous, .option, .add, .volume, .shuffle {
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
}
.play:hover, .next:hover, .previous:hover, .option:hover, .add:hover, .volume:hover, .shuffle:hover {
    color: #697e94;
}
.four {
    grid-area: four;
    grid-column: 2;
    border: 1px solid #d8dbe2;
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.four p {
    margin-top: 8px;
}
#date {
    color: #b7cee8;
    font-family: 'PT Mono', monospace;
    font-size: 1.3em;
    font-weight: 900;
    letter-spacing: 2px;
}
.five {
    grid-area: five;
    grid-column: 2;
    background: #b7cee8;
    overflow: scroll;
    border: 1px solid #d8dbe2;
}
.things li {
    display: inline-block;
}
.things label {
    flex: 1 1;
    cursor: pointer;
}
.things input {
    display: none;
}
.addItems {
    text-align: center;
}
.addItems input {
    width: 200px;
    height: 40px;
    font-weight: 300;
    text-align: center;
    color: #fff;
}
input::-webkit-input-placeholder {
    color: #fff;
}
input::-moz-placeholder {
    color: #fff;
}
input:-ms-input-placeholder {
    color: #fff;
}
input::-ms-input-placeholder {
    color: #fff;
}
input::placeholder {
    color: #fff;
}
button {
    background-color: #b7cee8;
    width: 200px;
    height: 40px;
    font-weight: 300;
    text-align: center;
    color: #fff;
    box-shadow: 1px 2px 1px 1px darkslategray;
    cursor: pointer;
}
span {
    cursor: pointer;
}
.reminders {
    display: flex;
    position: relative;
}
.reminders p {
    margin-top: 20px;
    margin-left: 20px;
}
.reminder--btns {
    position: absolute;
    right: 18px;
    top: 17px;
}
.reminder--btns i {
    color: #fff;
    cursor: pointer;
}
.reminder--btns i:hover {
    color: #577399;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}
.todo {
    cursor: pointer;
    display: inline-block;
}
.todo-switch {
    display: inline-block;
    background: #fff;
    border-radius: 50%;
    width: 12px;
    height: 12px;
    position: relative;
    vertical-align: middle;
    -webkit-transition: background 0.25s;
    transition: background 0.25s;
}
.todo-switch:before, .todo-switch:after {
    content: "";
    color: #fff;
}
.todo-checkbox:checked + .todo-switch {
    background: #577399;
}
.todo-checkbox:checked + .todo-switch:before {
    content: "\2713";
    color: #fff;
    font-size: 1.3em;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
    position: absolute;
    top: -10px;
    left: 1px;
}
.todo-checkbox {
    position: absolute;
    visibility: hidden;
}
.todo-label {
    font-size: 0.8em;
    margin-left: 5px;
    text-transform: uppercase;
    position: relative;
    color: #495867;
    text-transform: none;
}
li:hover {
    background: #b7cee8;
}
.hidden {
    display: none;
}
.inline {
    display: inline-block;
}
.space {
    display: inline;
    border: 1px sold blue;
}
