    height: 90vh;
    overflow: scroll;
    bottom: 0;
    padding-top: 50px;
}

.headline {
    font-size: 40px;
    float: right;
    color: #495867;
}

.about a {
    padding: 20px;
    text-align: center;
    display: inline;
    color: #495867;
    text-transform: uppercase;
    font-size: 16px;
    letter-spacing: 1px;
    cursor: pointer;
}
.about a:hover {
